<template>
  <div class="pull-left main-box-left">
      <tab-nav-red></tab-nav-red>
      <div class="news-box">
          <ul>
              <!-- "sportsCategoryName": "Ligue",
                  "code": "3001",
                  "title": "卡卡：内马尔本该排金球奖前5；姆巴佩让我想起罗纳尔多",
                  "summary": "虎扑12月9日讯 巴西球星卡卡近日接受了《巴黎人报》的采访。在访谈中他谈到了今年的金球奖，同时他表示，自己愿意未来为巴黎工作。",
                  "coverImageUrl": "https://c2.hoopchina.com.cn/uploads/star/event/images/181209/c11a97f7c06d03165e74c4ac7cc3eb09ad222fb9.jpg?x-oss-process=image/resize,w_400/format,jpg",
                  "source": "巴黎人报",
                  "publishTime": "2018-12-09T17:09:59.47",
                  "id": 349
                  =============
                  sportsCategoryName (string, optional): 类别名称 ,
                  code (string, optional): 站点源Code ,
                  title (string, optional): 标题 ,
                  summary (string, optional): 摘要 ,
                  coverImageUrl (string, optional): 封面图片Url ,
                  source (string, optional): 来源 ,
                  publishTime (string, optional): 发布时间 ,
                  id (integer, optional)-->
              <li class="clearfix" v-for="item in sportsNewsList" :key="item.id">
                  <a target="_blank" href="javascript:;" :title="item.title" class="pull-left info-img" @click="navigateTo(item.id)">
                      <img :src="item.coverImageUrl ? item.coverImageUrl : '../../static/images/400603.jpg?3944'" width="216" height="140" :alt="item.title">
                  </a>
                  <div class="pull-left info-art">
                      <h2 class="text-hidden">
                          <a target="_blank" href="javascript:;" :title="item.title" @click="navigateTo(item.id)">{{item.title}}</a>
                      </h2>
                      <p class="text-999">{{item.summary}}</p>
                      <div class="clearfix  info-tag">
                          <span><a href="javascript:;">{{item.sportsCategoryName}}</a></span>
                          <span class="text-999">{{item.time}}</span>
                      </div>
                  </div>
              </li>
          </ul>
          <!-- <pagination></pagination> -->
      </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToBeforeTime } from '@/utils'
import tabNavRed from '@/components/public/tabNavRed'
import pagination from '@/components/public/pagination'
export default {
  components: {
    'tab-nav-red': tabNavRed,
    'pagination': pagination
  },
  computed: {
    ...mapGetters({
      sportsNews: 'sportsNews'
    }),
    sportsNewsList () {
      let list = this.sportsNews.slice(0)
      list.forEach(item => {
        // 1小时内显示 xx分钟前
        // 1天内显示 xx小时前
        // 1月内显示 xx天前
        // 1月后显示 xx月xx日
        item.time = formatToBeforeTime(item.publishTime)
      })
      return list
    }
  },
  methods: {
    navigateTo (id) {
      this.$router.push({ name: 'article', params: { id: id } })
    }
  }
}
</script>

<style>

</style>



