<template>
  <div class="live-swiper">
    <div class="content">
        <div id="carousel-example-generic" class="carousel slide" data-ride="carousel"  data-interval="3000">
            <!-- Wrapper for slides -->
            <div class="carousel-inner" role="listbox">
                <div v-for="(live, index) in popularLiveSportsItemList" :key="'live' + index" class="item active">
                    <ul class="clearfix live-swiper-ul">
                        <li v-for="(item, index) in live" :key="'item' + index">
                            <div class="live-item">
                                <div class="clearfix li-live-title">
                                    <span class="pull-left match-name">
                                        {{item.categoryAlias}}
                                    </span>
                                    <time class="pull-right" :class="[item.status == 2 ? 'text-red' : 'text-blue']">{{item.statusName}}</time>
                                </div>
                                <div class="clearfix team-home">
                                    <span class="pull-left tea-name text-hidden">
                                        <img :src="item.leftBadgeUrl" width="28" height="31" alt="">{{item.leftGoal}}
                                    </span>
                                    <span class="pull-right strong team-score" :class="[item.status == 2 ? 'text-red' : 'text-blue']">{{item.leftScore}}</span>
                                </div>
                                <div class="clearfix team-away">
                                    <span class="pull-left team-name text-hidden">
                                          <img :src="item.rightBadgeUrl" width="28" height="31" alt="">{{item.rightGoal}}
                                    </span>
                                    <span class="pull-right strong team-score" :class="[item.status == 2 ? 'text-red' : 'text-blue']">{{item.rightScore}}</span>
                                </div>
                                <div class="live-type">
                                    <!-- status (integer, optional): 比赛状态,0 : 未开始 1: 直播中 2:已结束 = ['0', '1', '2'], -->
                                    <a href="javascript:;" target="_blank" class="pull-left live-state" v-if="item.status == 1" @click="navigateTo(0, item.id)">{{item.statusName}}</a>
                                    <!-- hasVideo (boolean, optional): 是否有录像[正在直播入口判断] ,
                                    hasHighlights (boolean, optional): 是否有锦集[正在直播入口判断] , -->
                                    <a href="javascript:;" target="_blank" class="pull-left end-state" v-if="item.hasVideo" @click="navigateTo(1, item.id)">录像</a>
                                    <a href="javascript:;" target="_blank" class="pull-left end-state" v-if="item.hasHighlights" @click="navigateTo(2, item.id)">集锦</a>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>

            <!-- Controls -->
            <a class="left carousel-control" href="#carousel-example-generic" role="button" data-slide="prev">
                <span></span>
                <!--<span class="sr-only">Previous</span>-->
            </a>
            <a class="right carousel-control" href="#carousel-example-generic" role="button" data-slide="next">
                <span></span>
                <!--<span class="sr-only">Next</span>-->
            </a>
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      popularLiveSportsItems: 'popularLiveSportsItems'
    }),
    popularLiveSportsItemList () {
      let list = this.popularLiveSportsItems
      let items = {}
      let page = 0
      list.forEach((x, index) => {
        items[page] || (items[page] = [])
        items[page].push(x)
        if (index !== 0 && (index + 1) % 5 == 0) {
          page++
        }
      })

      return items
    }
  },
  methods: {
    navigateTo (videoCategory, videoId) {
      // videoCategory 视频分类, 0:直播 1:录像 2:集锦 99:其他
      this.$router.push({ name: 'live', params: { videoCategory: videoCategory, videoId: videoId } })
    }
  },
  created () {
  }
}
</script>

<style>
</style>



