<template>
    <header class="layout-header">
    <div class="content">
        <div class="header-content">
            <h1 class="logo"></h1>
            <ul class="clearfix header-nav">
                <li :class="[item.name == activeHeaderNav ? 'active' : '']" v-for="item in headerNav" :key="item.path">
                  <a href="javascript:void(0);" @click="navigateTo(item.name)">{{item.text}}</a>
                </li>
            </ul>
            <ul class="collect-nav">
                <li><a href="javascript:void(0);" @click="addFavorite('http://www.baidu.com', '百度一下，你就知道')">加收藏</a></li>
                <li><a href="javascript:void(0);" @click="toDesktop('http://www.baidu.com', '百度一下，你就知道')">桌面快捷</a></li>
            </ul>
        </div>
    </div>
  </header>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      headerNav: [
        {
          path: '/',
          name: 'index',
          text: '首页'
        },
        {
          path: '/video',
          name: 'video',
          text: '集锦／录像'
        },
        {
          path: '/news',
          name: 'news',
          text: '资讯'
        },
        {
          path: '/live',
          name: 'live',
          text: '视频'
        },
        {
          path: '/channel',
          name: 'channel',
          text: '频道列表'
        },
        {
          path: '/bflive',
          name: 'bflive',
          text: '即时比分'
        }
      ]
    }
  },
  computed: {
    ...mapGetters({
      activeHeaderNav: 'activeHeaderNav'
    })
  },
  methods: {
    ...mapActions([
      'setActiveHeaderNav'
    ]),
    navigateTo (name) {
      this.setActiveHeaderNav(name)
      this.$router.push({ name: name })
    },
    // 加入收藏 <a onclick="AddFavorite(window.location,document.title)">加入收藏</a>
    addFavorite (sURL, sTitle) {
      try {
        window.external.addFavorite(sURL, sTitle)
      } catch (e) {
        try {
          window.sidebar.addPanel(sTitle, sURL, '')
        } catch (e) {
          alert('加入收藏失败，请使用Ctrl+D进行添加')
        }
      }
    },
    toDesktop (sUrl, sName) {
      try {
        var WshShell = new window.ActiveXObject('WScript.Shell')
        var oUrlLink = WshShell.CreateShortcut(WshShell.SpecialFolders('Desktop') + '\\' + sName + '.url')
        oUrlLink.TargetPath = sUrl
        oUrlLink.Save()
      } catch (e) {
        alert('当前IE安全级别不允许操作！')
      }
    }
  }
}
</script>

<style>
</style>



