<template>
      <div class="content">
        <div class="fan-com">
            <div class="clearfix fan-header">
                <h2 class="pull-left">球迷社群 <i class="icon-fan"></i></h2>
                <ul class="pull-left fan-nav">
                    <li v-for="(item, index) in fanCommunityCategories"
                      :key="item.id"
                      :class="[activeIndex == index ? 'active' : '']"
                      @click="changeFanCommunityCategory(item.id, index)">
                      <a href="javascript:;">{{item.cnName}}</a>
                    </li>
                </ul>
            </div>
            <div class="fan-list">
                <ul class="clearfix">
                    <li class="item" v-for="item in fanCommunityAd" :key="item">
                        <div class="fan-item">
                            <div class="win-rate">100%胜率</div>
                            <div class="clearfix fan-info">
                                <div class="pull-left face-img"><img alt="无敌是最寂" title="" src="../../static/images/icon-football.png"></div>
                                <div class="pull-left fan-name">
                                    <p class="fs16">无敌是最寂寞</p>
                                    <span>10中9</span><span>10连红</span>
                                </div>
                            </div>
                            <p class="fan-special">专业研究亚盘十年，擅长五大联赛、日本联赛</p>
                            <div class="fan-group clearfix">
                                <ul class="pull-left">
                                    <li><img src="../../static/images/icon-football.png" width="20" height="20" alt="">微信群：356564673</li>
                                    <li><img src="../../static/images/icon-football.png" width="20" height="20" alt="">微信群：356564673</li>
                                </ul>
                                <div class="pull-right group-ewm">
                                    <img src="../../static/images/wexin-group.jpg" alt="">
                                </div>
                            </div>
                            <!-- TODO: 空的时候加蒙版 -->
                            <!-- adImageUrl (string, optional): 广告图片url ,
                              adUrl (string, optional): 点击广告Url ,
                              qrCodeUrl (string, optional): 二维码图片Url ,
                              id (integer, optional)
                              ======================
                              {
                                "adImageUrl": "http://www.qq.com",
                                "adUrl": "http://www.baidu.com",
                                "qrCodeUrl": "http://www.google.com",
                                "id": 1
                              }-->
                            <div class="cover-vague" v-if="item.empty">
                                <a href="javascript:;" class="rec-coop">招募大咖推荐合作</a>
                                <p>空位正待</p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      fanCommunityCategories: 'fanCommunityCategories',
      fanCommunityAd: 'fanCommunityAd'
    })
  },
  methods: {
    ...mapActions([
      'setFanCommunityAdByCategory'
    ]),
    changeFanCommunityCategory (id, index) {
      this.activeIndex = index
      this.setFanCommunityAdByCategory(id)
    }
  }
}
</script>

<style>

</style>



