<template>
  <div class="pull-left main-box-left">
    <div class="channel-box">
      <ul class="channel-list clearfix">
        <!-- "name": "CCTV5-体育频道",
          "logoUrl": "http://p1.img.cctvpic.com/photoAlbum/page/performance/img/2018/5/18/1526623711375_17.jpg",
          "liveUrl": "http://www.cctv5.com",
          "parameters": "",
          "programLists": [
            {
              "startTime": "2018-12-06T22:15:00",
              "title": "2018/2019赛季冰壶世界杯美国站-混双循环赛(美国-中国)",
              "id": 1
            }
          ]
          =========================
          name (string, optional): 频道名称 ,
          logoUrl (string, optional): 台标Url ,
          liveUrl (string, optional): 直播Url ,
          parameters (string, optional): 直播参数 ,
          programLists (Array[ZLL.KYZB.LiveSports.AppServices.Dtos.ChannelProgramListDto], optional): 节目清单 ,
          id (integer, optional)
          ==========================
          startTime (string, optional): 节目开始时间 ,
          title (string, optional): 节目标题 ,
          id (integer, optional)-->
        <li v-for="(item) in channelList" :key="item.id" class="pull-left" v-if="channelList.length > 0">
          <a :href="item.liveUrl">
            <h1 class="channel-title">
              <img :src="item.logoUrl" class="logo" />
              {{item.name}}
            </h1>
            <div class="program-box">
              <ul class="program-list">
                <li v-for="(program) in item.programLists" :key="program.id">
                  {{program.time}} {{program.title}}
                </li>
              </ul>
            </div>
          </a>
        </li>
        <li v-if="channelList.length == 0" style="border: 0 none;">暂无数据...</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToHHmm } from '@/utils'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapGetters({
      channel: 'channel'
    }),
    channelList () {
      let list = this.channel
      if (list.length < 1) return []
      list.forEach(x => {
        x.programLists.forEach(c => {
          c.time = formatToHHmm(c.startTime)
        })
      })
      return list
    }
  }
}
</script>

<style>
.channel-box{
  background: #fff;
  padding: 13px 0 13px 9px;
}
.channel-list>li{
  width: 188px;
  margin-right: 9px;
  margin-bottom: 10px;
  border: 1px solid #eaeaea;
}
</style>
<style scoped>
.logo{
  max-width: 40px;
  max-height: 16px;
}
.channel-title{
  padding: 8px;
  border-bottom: 1px solid #eaeaea;
  font-size: 14px;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.program-box{
  height: 140px;
  padding: 8px;
  overflow: hidden;
}
.program-list{
  height: 100%;
  overflow: hidden;
}
.program-list>li{
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>



