export const setStore = (name, content, time) => {
  if (!name) return
  if (typeof content !== 'string') {
    content = JSON.stringify(content)
  }
  time || (time = 60)
  let cacheExpireDate = (new Date() - 1) + time * 1000
  content = { val: content, exp: cacheExpireDate }
  window.localStorage.setItem(name, JSON.stringify(content))
}

export const getStore = (name) => {
  if (!name) return
  let content = window.localStorage.getItem(name) || ''
  if (content == 'undefined' || content == '') {
    return ''
  }
  let cacheObj = JSON.parse(content)
  if (cacheObj.exp < new Date() - 1) {
    removeStore(name)
    content = ''
  } else {
    content = cacheObj.val
  }
  return content
}

export const removeStore = name => {
  if (!name) return
  window.localStorage.removeItem(name)
}

// 1位数前补0
export const add0ToBefore = (n) => {
  return n < 10 ? '0' + n : n
}

// 日期转月-日：返回如 01-01
export const formatToMonthDay = (datetime) => {
  let date = formatDateTime(datetime)
  return date.MM + '-' + date.dd
}

// 日期转中文月日：返回如 01月01日
export const formatToMonthDayZh = (datetime) => {
  let date = formatDateTime(datetime)
  return date.MM + '月' + date.dd + '日'
}

// 日期转年-月-日8位：返回如 2019-01-01
export const formatToyyyyMMdd8 = (datetime) => {
  let date = formatDateTime(datetime)
  return date.yyyy + '-' + date.MM + '-' + date.dd
}

// 日期转年月日6位：返回如 20190101
export const formatToyyyyMMdd = (datetime) => {
  let date = formatDateTime(datetime)
  return date.yyyy + date.MM + date.dd
}

// 日期转小时分钟：返回如 01:01
export const formatToHHmm = (datetime) => {
  if (!datetime) return
  let date = formatDateTime(datetime)
  return date.hh + ':' + date.mm
}

// n久前
export const formatToBeforeTime = (datetime) => {
  // 1小时内显示 xx分钟前 <60*60*1000 = 3.6e6
  // 1天内显示 xx小时前 <24*60*60*1000 = 8.64e7
  // 1月内显示 xx天前 <30*24*60*60*1000 = 2592000000 = 2.592e9
  // 1月后显示 xx月xx日 >30*24*60*60*1000 = 2.592e9
  let between = new Date().getTime() - new Date(datetime).getTime()
  if (between > 0) {
    if (between < 3.6e6) {
      let mm = parseInt(between / (6e4))
      return `${mm}分钟前`
    }
    if (between < 8.64e7) {
      let HH = parseInt(between / 3.6e6)
      return `${HH}小时前`
    }
    if (between < 2.592e9) {
      let dd = parseInt(between / 8.64e7)
      return `${dd}天前`
    }
    if (between > 2.592e9) {
      let dateObj = formatDateTime(datetime)
      return `${dateObj.MM}月${dateObj.dd}日`
    }
  }
}

// 获取时间对象
export const formatDateTime = (datetime) => {
  if (!datetime) return
  let date = new Date(datetime)
  let MM = add0ToBefore(date.getMonth() + 1)
  let dd = add0ToBefore(date.getDate())
  let hh = add0ToBefore(date.getHours())
  let mm = add0ToBefore(date.getMinutes())
  let ss = add0ToBefore(date.getSeconds())
  let dateObj = {
    yyyy: date.getFullYear(),
    MM: MM,
    dd: dd,
    hh: hh,
    mm: mm,
    ss: ss
  }
  return dateObj
}



