<template>
  <div>
    <app-header></app-header>
    <live-list></live-list>
    <app-home></app-home>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import header from '@/components/public/header'
import liveList from '@/components/liveList'
import homeMain from '@/components/homeMain'
import footer from '@/components/public/footer'
import api from '@/api'
export default {
  components: {
    'app-header': header,
    'live-list': liveList,
    'app-home': homeMain,
    'app-footer': footer
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      token: 'token',
      popularLiveSportsItemsTotalCount: 'popularLiveSportsItemsTotalCount'
    }),
    popularLiveSportsItemsList () {
      return this.popularLiveSportsItems
    }
  },
  methods: {
    ...mapActions([
      'setToken',
      'setPopularLiveSportsItems',
      'setPopularLiveSportsItemsTotalCount',
      'setPopularVideoCategories',
      'setPopularVideos',
      'setLiveSportsCategories',
      'setLiveSportsItemsByCategory'
    ]),
    async setTokenAsync () {
      try {
        let value = await this.setToken()
        if (value) {
          this.setPopularLiveSportsItems()
          this.setPopularVideoCategories()
          this.setPopularVideos()
          this.setLiveSportsCategories()
          this.setLiveSportsItemsByCategory()
        }
      } catch (err) {
        console.log(err)
      }
    }
  },
  created () {
    if (!this.token) {
      this.setTokenAsync()
    }
  }
}
</script>

<style>
@import "../../static/css/index/index.css";
</style>



