<template>
  <div class="hot-video">
      <div class="hot-video-nav clearfix">
          <h2 class="pull-left">热门视频</h2>
          <ul class="pull-left">
              <li v-for="(item, index) in popularVideoCategories" :key="item.id"><a href="javascript:void(0);" @click="changeTab(item.id, index)">{{item.cnName}}</a></li>
          </ul>
      </div>
      <div class="hot-video-list">
          <ul class="row">
              <li class="cover-mask-hover play-btn-hover" v-for="item in popularVideos" :key="item.id">
                <!-- "sportsCategoryId": 0,
                    "leftGoal": "string",
                    "rightGoal": "string",
                    "categoryAlias": "string",
                    "startTime": "2018-12-11T01:48:54.713Z",
                    "coverImageUrl": "string",
                    "videoCategory": 0,
                    "videoCategoryName": "string",
                    "id": 0
                    ============
                    sportsCategoryId (integer, optional): 项目类别Id ,
                    leftGoal (string, optional): 左边球队名称 ,
                    rightGoal (string, optional): 右边球队名称 ,
                    categoryAlias (string, optional): 比赛类别别名,如NBA常规赛 ,
                    startTime (string, optional): 开赛时间 ,
                    coverImageUrl (string, optional): 封面图片Url(只有集锦或者录像才有值) ,
                    videoCategory (integer, optional): 视频分类, 0:直播 1:录像 2:集锦 99:其他 = ['0', '1', '2', '99'],
                    videoCategoryName (string, optional, read only): 视频类别名称 ,
                    id (integer, optional)-->
                  <a href="javascript:;" target="_blank" :title="item.categoryAlias" @click="navigateTo(item.videoCategory, item.id)">
                      <img :src="item.coverImageUrl ? item.coverImageUrl : '../../static/images/pexels-photo-968571.jpeg'"
                        width="100%" height="100%"
                        :alt="item.categoryAlias">
                      <span class="trapezoid bg-blue" v-if="item.videoCategory == 2">集锦</span>
                      <span class="trapezoid bg-blue" v-if="item.videoCategory == 99">其他</span>
                      <span class="trapezoid bg-red" v-if="item.videoCategory == 1">录像</span>
                      <i class="cover-mask"></i>
                      <i class="play-btn"></i>
                  </a>
                  <p class="text-hidden" :title="item.leftGoal + 'vs' + item.rightGoal + ' ' + item.videoCategoryName">{{item.leftGoal}}vs{{item.rightGoal}} {{item.videoCategoryName}}</p>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      popularVideoCategories: 'popularVideoCategories',
      popularVideos: 'popularVideos'
    })
  },
  methods: {
    ...mapActions([
      'setPopularVideos'
    ]),
    changeTab (id, index) {
      // console.log(id)
      this.activeIndex = index
      this.setPopularVideos(id)
    },
    navigateTo (videoCategory, videoId) {
      // videoCategory 视频分类, 0:直播 1:录像 2:集锦 99:其他
      this.$router.push({ name: 'live', params: { videoCategory: videoCategory, videoId: videoId } })
    }
  }
}
</script>

<style>

</style>



