<template>
  <div>
    <app-header></app-header>
    <div class="content" style="margin-top: 24px;">
      <div class="content-main">
          <div class="main-box clearfix">
            <tab-nav-red></tab-nav-red>
            <channel></channel>
            <div class="pull-right main-box-right">
              <qrcode></qrcode>
            </div>
          </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import header from '../components/public/header'
import tabNavRed from '@/components/public/tabNavRed'
import channel from '../components/channel'
import qrcode from '../components/public/QRCode'
import footer from '../components/public/footer'
export default {
  data () {
    return {}
  },
  components: {
    'app-header': header,
    'tab-nav-red': tabNavRed,
    'channel': channel,
    'qrcode': qrcode,
    'app-footer': footer
  },
  computed: {
    ...mapGetters({
      channelCategories: 'channelCategories'
    }),
    params () {
      return (this.channelCategories[0] && this.channelCategories[0].id) || -1
    }
  },
  methods: {
    ...mapActions([
      'setChannelCategories',
      'setChannelByCategory'
    ])
  },
  created () {
    this.setChannelCategories()
    this.setChannelByCategory(this.params)
  }
}
</script>

<style>
@import "../../static/css/live/live.css";
</style>



