<template>
  <div class="match-nav">
    <ul class="clearfix">
      <!-- "name": "All",
          "cnName": "全部",
          "id": -1 -->
      <li v-for="(item, index) in liveSportsCategories" :key="item.id" :class="[index == activeIndex ? 'active' : '']" @click="changeTab(item.id, index)">
          <a href="javascript:void(0);">{{item.cnName}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      liveSportsCategories: 'liveSportsCategories'
    })
  },
  methods: {
    ...mapActions([
      'setLiveSportsItemsByCategory'
    ]),
    changeTab (id, index) {
      this.activeIndex = index
      this.setLiveSportsItemsByCategory(id)
    }
  }
}
</script>

<style>

</style>



