<template>
  <div class="sports-news">
      <div class="clearfix sports-news-header">
          <h2 class="pull-left title">体育头条</h2>
          <a class="pull-right text-999" href="/news">更多></a>
      </div>
      <ul class="news-list-ul">
          <!-- sportsCategoryName (string, optional): 类别名称 ,
            code (string, optional): 站点源Code ,
            title (string, optional): 标题 ,
            summary (string, optional): 摘要 ,
            coverImageUrl (string, optional): 封面图片Url ,
            source (string, optional): 来源 ,
            publishTime (string, optional): 发布时间 ,
            id (integer, optional) -->
          <li v-for="item in headlines" :key="item.id" @click="navigateTo(item.id)">
            <a href="javascript:;" class="text-hidden">
              <span class="bg-red text-uppercase">{{item.sportsCategoryName || '默认'}}</span>
              {{item.title}}
            </a>
          </li>
      </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      headlines: 'headlines'
    })
  },
  methods: {
    ...mapActions([
      'setHeadlines'
    ]),
    navigateTo (id) {
      this.$router.push({ name: 'article', params: { id: id } })
    }
  },
  created () {
    this.setHeadlines()
  }
}
</script>

<style>

</style>



