import Vue from 'vue'
import Vuex from 'vuex'
import createLogger from 'vuex/dist/logger'
import api from '@/api'
import { setStore, getStore } from '@/utils'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

const store = new Vuex.Store({
  state: {
    activeHeaderNav: 'index',
    token: '',
    // [Web]获取首页热门直播项目(注:只有在请求中SkipCount=0时才会返回TotalCount值)
    popularLiveSportsItems: [],
    popularLiveSportsItemsTotalCount: 0,
    // [Web]获取热门视频分类
    popularVideoCategories: [],
    // [Web]获取热门视频
    popularVideos: [],
    // [Web]获取直播项目分类
    liveSportsCategories: [],
    // [Web]根据体育分类查询直播项目列表
    liveSportsItems: [],
    // [Web]获取直播体育项目的直播源
    liveSportsItemSources: [],
    // 获取集锦/录像视频分类
    highlightsVideoCategories: [],
    // [Web]根据分类查询集锦/录像,只有在SkipCount = 0时才返回TotalCount
    highlightsSportsItem: [],
    highlightsSportsItemTotalCount: 0,
    highlightsSportsItemCategoryId: -1,

    // [Web]获取体育资讯项目分类
    sportsNewsCategories: [],
    // [Web]根据分类获取体育资讯(注: 只有skipcount= 0时才会返回TotalCount)
    sportsNews: [],
    sportsNewsLoading: false,
    setSportsNewsTotalCount: 0,
    sportsNewsCategoryId: -1,
    // [Web]获取体育资讯的内容
    sportsNewsContent: {},
    // [Web]获取体育资讯头条(注:供直播页面的体育头条使用)
    headlines: [],

    // [Web]获取频道类别
    channelCategories: [],
    // [Web]根据类别查询频道列表
    channel: [],

    // [Web]获取球迷社群分类列表
    fanCommunityCategories: [],
    // [Web]根据分类获取广告列表
    fanCommunityAd: []
  },
  getters: {
    activeHeaderNav: (state) => {
      return state.activeHeaderNav
    },
    token: (state) => {
      return state.token
    },
    popularLiveSportsItems: (state) => {
      return state.popularLiveSportsItems
    },
    popularLiveSportsItemsTotalCount: (state) => {
      return state.popularLiveSportsItemsTotalCount
    },
    popularVideoCategories: (state) => {
      return state.popularVideoCategories
    },
    popularVideos: (state) => {
      return state.popularVideos
    },
    liveSportsCategories: (state) => {
      return state.liveSportsCategories
    },
    liveSportsItems: (state) => {
      return state.liveSportsItems
    },
    liveSportsItemSources: (state) => {
      return state.liveSportsItemSources
    },
    highlightsSportsItem: (state) => {
      return state.highlightsSportsItem
    },
    highlightsVideoCategories: (state) => {
      return state.highlightsVideoCategories
    },
    highlightsSportsItemTotalCount: (state) => {
      return state.highlightsSportsItemTotalCount
    },
    highlightsSportsItemCategoryId: (state) => {
      return state.highlightsSportsItemCategoryId
    },
    // 新闻
    sportsNewsCategories: (state) => {
      return state.sportsNewsCategories
    },
    sportsNews: (state) => {
      return state.sportsNews
    },
    sportsNewsLoading: (state) => {
      return state.sportsNewsLoading
    },
    sportsNewsCategoryId: (state) => {
      return state.sportsNewsCategoryId
    },
    sportsNewsContent: (state) => {
      return state.sportsNewsContent
    },
    headlines: (state) => {
      return state.headlines
    },
    channelCategories: (state) => {
      return state.channelCategories
    },
    channel: (state) => {
      return state.channel
    },
    fanCommunityCategories: (state) => {
      return state.fanCommunityCategories
    },
    fanCommunityAd: (state) => {
      return state.fanCommunityAd
    }
  },
  mutations: {
    setActiveHeaderNav (state, payload) {
      state.activeHeaderNav = payload
    },
    setToken (state, payload) {
      state.token = payload
    },
    setPopularLiveSportsItems (state, payload) {
      state.popularLiveSportsItems = payload
    },
    setPopularLiveSportsItemsTotalCount (state, payload) {
      state.popularLiveSportsItemsTotalCount = payload
    },
    setPopularVideoCategories (state, payload) {
      state.popularVideoCategories = payload
    },
    setPopularVideos (state, payload) {
      state.popularVideos = payload
    },
    setLiveSportsCategories (state, payload) {
      state.liveSportsCategories = payload
    },
    setLiveSportsItems (state, payload) {
      state.liveSportsItems = payload
    },
    setLiveSportsItemSources (state, payload) {
      state.liveSportsItemSources = payload
    },
    setSportsNewsCategories (state, payload) {
      state.sportsNewsCategories = payload
    },
    setSportsNews (state, payload) {
      state.sportsNews = payload
    },
    setSportsNewsLoading (state, payload) {
      state.sportsNewsLoading = payload
    },
    setSportsNewsTotalCount (state, payload) {
      state.sportsNewsTotalCount = payload
    },
    setSportsNewsCategoryId (state, payload) {
      state.sportsNewsCategoryId = payload
    },
    setSportsNewsContent (state, payload) {
      state.sportsNewsContent = payload
    },
    setHeadlines (state, payload) {
      state.headlines = payload
    },
    setHighlightsVideoCategories (state, payload) {
      state.highlightsVideoCategories = payload
    },
    setHighlightsSportsItem (state, payload) {
      state.highlightsSportsItem = payload
    },
    setHighlightsSportsItemTotalCount (state, payload) {
      state.highlightsSportsItemTotalCount = payload
    },
    setHighlightsSportsItemCategoryId (state, payload) {
      state.highlightsSportsItemCategoryId = payload
    },
    setChannelCategories (state, payload) {
      state.channelCategories = payload
    },
    setChannel (state, payload) {
      state.channel = payload
    },
    setFanCommunityCategories (state, payload) {
      state.fanCommunityCategories = payload
    },
    setFanCommunityAd (state, payload) {
      state.fanCommunityAd = payload
    }
  },
  actions: {
    setActiveHeaderNav ({ commit }, payload) {
      commit('setActiveHeaderNav', payload)
    },
    setToken ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        if (getStore('access-token') != '') {
          commit('setToken', getStore('access-token'))
          resolve(true)
        } else {
          let params = {
            userName: 'guest',
            password: 'guest2018',
            terminalType: 0
          }
          api.post('/api/services/userAccounts/account/Login', params).then(res => {
            if (res.data.success) {
              setStore('access-token', res.data.result)
              commit('setToken', res.data.result)
              resolve(true)
            } else {
              reject(new Error('错误'))
            }
          })
        }
      })
    },
    setPopularLiveSportsItems ({ commit, state }, payload) {
      let params = Object.assign({}, {
        skipCount: 0,
        maxResultCount: 5
      }, payload)
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetPopularLiveSportsItems',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setPopularLiveSportsItems', res.data.result.items)
            commit('setPopularLiveSportsItemsTotalCount', res.data.result.totalCount)
          }
        })
    },
    setPopularVideoCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetPopularVideoCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            // this.setPopularVideoCategories(res.data.result.items)
            commit('setPopularVideoCategories', res.data.result.items)
          }
        })
    },
    setPopularVideos ({ commit, state }, payload) {
      let params = {
        categoryId: payload || -1
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetPopularVideos',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            // this.setPopularVideos(res.data.result.items)
            commit('setPopularVideos', res.data.result.items)
          }
        })
    },
    setLiveSportsCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetLiveSportsCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            // this.setLiveSportsCategories(res.data.result.items)
            commit('setLiveSportsCategories', res.data.result.items)
          }
        })
    },
    setLiveSportsItemsByCategory ({ commit, state }, payload) {
      let params = {
        categoryId: payload || -1
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetLiveSportsItemsByCategory',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setLiveSportsItems', res.data.result.items)
          }
        })
    },
    setLiveSportsItemSources ({ commit, state }, payload) {
      let params = Object.assign({}, {
        videoCategory: 0,
        id: 0
      }, payload)
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetLiveSportsItemSources',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setLiveSportsItemSources', res.data.result.items)
          }
        })
    },
    setSportsNewsCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/sportsNews/GetSportsNewsCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setSportsNewsCategories', res.data.result.items)
          }
        })
    },
    setSportsNewsByCategory ({ commit, state }, payload) {
      let params = Object.assign({}, {
        categoryId: -1,
        skipCount: 0,
        maxResultCount: 5
      }, payload.params)
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/sportsNews/GetSportsNewsByCategory',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            let sportsNews = res.data.result.items
            if (payload.keep) {
              sportsNews = state.sportsNews.concat(sportsNews)
            }
            commit('setSportsNewsLoading', false)
            commit('setSportsNews', sportsNews)
            commit('setSportsNewsTotalCount', res.data.result.totalCount)
          }
        })
    },
    setSportsNewsLoading ({ commit, state }, payload) {
      commit('setSportsNewsLoading', payload)
    },
    setSportsNewsCategoryId ({ commit, state }, payload) {
      commit('setSportsNewsCategoryId', payload)
    },
    setSportsNewsContentById ({ commit, state }, payload) {
      let params = {
        id: payload || 0
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/sportsNews/GetSportsNewsContentById',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setSportsNewsContent', res.data.result)
          }
        })
    },
    setHeadlines ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/sportsNews/GetHeadlines',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setHeadlines', res.data.result.items)
          }
        })
    },
    setHighlightsVideoCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetHighlightsVideoCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setHighlightsVideoCategories', res.data.result.items)
          }
        })
    },
    setHighlightsSportsItemByCategory ({ commit, state }, payload) {
      let params = Object.assign({}, {
        categoryId: -1,
        skipCount: 0,
        maxResultCount: 5
      }, payload)
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/liveSports/GetHighlightsSportsItemByCategory',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setHighlightsSportsItem', res.data.result.items)
            commit('setHighlightsSportsItemTotalCount', res.data.result.totalCount)
          }
        })
    },
    setHighlightsSportsItemCategoryId ({ commit, state }, payload) {
      commit('setHighlightsSportsItemCategoryId', payload)
    },
    setChannelCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/channel/GetChannelCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setChannelCategories', res.data.result.items)
          }
        })
    },
    setChannelByCategory ({ commit, state }, payload) {
      let params = {
        categoryId: payload || -1
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/channel/GetChannelByCategory',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setChannel', res.data.result.items)
          }
        })
    },
    setFanCommunityCategories ({ commit, state }, payload) {
      let params = {
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/fanCommunity/GetFanCommunityCategories',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setFanCommunityCategories', res.data.result.items)
          }
        })
    },
    setFanCommunityAdByCategory ({ commit, state }, payload) {
      let params = {
        categoryId: payload || -1
      }
      let config = {
        headers: {
          Authorization: 'Bearer ' + state.token
        }
      }
      api
        .post(
          '/api/services/liveSports/fanCommunity/GetFanCommunityAdByCategory',
          params,
          config
        )
        .then(res => {
          if (res.data.success) {
            commit('setFanCommunityAd', res.data.result.items)
          }
        })
    }
  },
  modules: {

  },
  strict: debug,
  plugins: debug ? [createLogger()] : []
})

export default store



