<template>
  <div>
    <app-header></app-header>
    <div class="content" style="margin-top: 24px;">
      <div class="content-main">
          <div class="main-box clearfix">
              <news-list></news-list>
              <div class="pull-right main-box-right">
                <qrcode></qrcode>
              </div>
          </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import header from '@/components/public/header'
import newsList from '@/components/newsList'
import QRCode from '@/components/public/QRCode'
import footer from '@/components/public/footer'
import api from '@/api'
export default {
  components: {
    'app-header': header,
    'news-list': newsList,
    'qrcode': QRCode,
    'app-footer': footer
  },
  data () {
    return {
      pageIndex: 1,
      pageSize: 20,
      lock: false
    }
  },
  computed: {
    ...mapGetters({
      sportsNewsCategories: 'sportsNewsCategories',
      sportsNewsLoading: 'sportsNewsLoading',
      sportsNewsCategoryId: 'sportsNewsCategoryId'
    }),
    // sportsNewsCategoryId () {
    //   return this.sportsNewsCategories[0] && this.sportsNewsCategories[0].id
    // },
    params () {
      return {
        categoryId: this.sportsNewsCategoryId || -1,
        skipCount: (this.pageIndex - 1) * this.pageSize,
        maxResultCount: this.pageSize
      }
    }
  },
  watch: {
    // 如果 `id` 发生改变，这个函数就会运行
    sportsNewsCategoryId: function (newId, oldId) {
      this.pageIndex = 1
    }
  },
  methods: {
    ...mapActions([
      'setSportsNewsCategories',
      'setSportsNewsByCategory',
      'setSportsNewsLoading',
      'setSportsNewsCategoryId'
    ]),
    // --------------上拉加载更多---------------
    // 获取滚动条当前的位置
    getScrollTop () {
      var scrollTop = 0
      if (document.documentElement && document.documentElement.scrollTop) {
        scrollTop = document.documentElement.scrollTop
      } else if (document.body) {
        scrollTop = document.body.scrollTop
      }
      return scrollTop
    },

    // 获取当前可视范围的高度
    getClientHeight () {
      var clientHeight = 0
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        clientHeight = Math.min(document.body.clientHeight, document.documentElement.clientHeight)
      } else {
        clientHeight = Math.max(document.body.clientHeight, document.documentElement.clientHeight)
      }
      return clientHeight
    },

    // 获取文档完整的高度
    getScrollHeight () {
      return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight)
    }
    // -----------------结束--------------------
  },
  created () {
    this.setSportsNewsCategories()
    this.setSportsNewsByCategory({ params: this.params })
    let _this = this
    // 滚动事件触发
    window.onscroll = function () {
      if (_this.$route.name != 'news') return
      if (_this.sportsNewsLoading) return
      if (_this.getScrollTop() + _this.getClientHeight() + 100 >= _this.getScrollHeight()) {
        if (_this.lock) return
        _this.lock = true
        _this.pageIndex++
        _this.setSportsNewsLoading(true)
        _this.setSportsNewsByCategory({ params: _this.params, keep: true })
        setTimeout(function () {
          _this.lock = false
        }, 1000)
      }
    }
  }
}
</script>

<style>
@import "../../static/css/news/news-list.css";
</style>



