import Vue from 'vue'
import store from '@/store'
import Router from 'vue-router'
import Index from '@/views/index'
import Article from '@/views/article'
import BfLive from '@/views/bfLive'
import Live from '@/views/live'
import News from '@/views/news'
import Channel from '@/views/channel'
import Video from '@/views/video'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta: { activeHeaderNav: 'index' }
    },
    {
      path: '/article/:id',
      name: 'article',
      component: Article,
      meta: { activeHeaderNav: 'news' }
    },
    {
      path: '/bflive',
      name: 'bflive',
      component: BfLive,
      meta: { activeHeaderNav: 'bflive' }
    },
    {
      path: '/live',
      name: 'live',
      component: Live,
      meta: { activeHeaderNav: 'live' }
    },
    {
      path: '/news',
      name: 'news',
      component: News,
      meta: { activeHeaderNav: 'news' }
    },
    {
      path: '/channel',
      name: 'channel',
      component: Channel,
      meta: { activeHeaderNav: 'channel' }
    },
    {
      path: '/video',
      name: 'video',
      component: Video,
      meta: { activeHeaderNav: 'video' }
    }
  ]
})

router.beforeEach((to, from, next) => {
  store.dispatch('setActiveHeaderNav', to.meta.activeHeaderNav)
  next()
})

export default router



