<template>
  <div>
    <app-header></app-header>
    <div class="content" style="margin-top: 24px;">
        <div class="content-main">
          <video-list></video-list>
        </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import header from '@/components/public/header'
import videoList from '@/components/videoList'
import footer from '@/components/public/footer'
export default {
  components: {
    'app-header': header,
    'video-list': videoList,
    'app-footer': footer
  },
  data () {
    return {
      pageIndex: 1,
      pageSize: 20,
      lock: false
    }
  },
  computed: {
    ...mapGetters({
      highlightsSportsItemLoading: 'highlightsSportsItemLoading',
      highlightsSportsItemCategoryId: 'highlightsSportsItemCategoryId'
    }),
    params () {
      return {
        categoryId: this.highlightsSportsItemCategoryId || -1,
        skipCount: (this.pageIndex - 1) * this.pageSize,
        maxResultCount: this.pageSize
      }
    }
  },
  methods: {
    ...mapActions([
      'setHighlightsVideoCategories',
      'setHighlightsSportsItemByCategory',
      'setHighlightsSportsItemLoading',
      'setHighlightsSportsItemCategoryId'
    ])
  },
  created () {
    this.setHighlightsVideoCategories()
    this.setHighlightsSportsItemByCategory(this.params)
  }
}
</script>

<style>
@import "../../static/css/videoList/video-list.css";
</style>



