<template>
  <div>
    <app-header></app-header>
    <div class="content" style="margin-top: 24px;">
        <div class="content-main">
            <div class="main-box clearfix">
                <div class="pull-left main-box-left">
                  <article-main></article-main>
                </div>
                <div class="pull-right main-box-right">
                    <qrcode></qrcode>
                    <relactive-info></relactive-info>
                    <ad></ad>
                </div>
            </div>
        </div>
    </div>

    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import header from '@/components/public/header'
import articleMain from '@/components/articleMain'
import QRCode from '@/components/public/QRCode'
import relactiveInfo from '@/components/relactiveInfo'
import ad from '@/components/public/ad'
import footer from '@/components/public/footer'
export default {
  components: {
    'app-header': header,
    'article-main': articleMain,
    'qrcode': QRCode,
    'relactive-info': relactiveInfo,
    'ad': ad,
    'app-footer': footer
  },
  methods: {
    ...mapActions([
      'setSportsNewsContentById'
    ])
  },
  created () {
    let id = this.$route.params.id
    this.setSportsNewsContentById(id)
  }
}
</script>

<style>
@import "../../static/css/article/article.css";
</style>



