<template>
  <div class="live-main">
    <div class="content">
        <!-- code (string, optional): 直播来源Code,例如腾讯体育,天天直播等 ,
            name (string, optional): 名称 ,
            videoCategory (integer, optional): 视频分类, 0:直播 1:录像 2:集锦 99:其他 = ['0', '1', '2', '99'],
            liveUrl (string, optional): 直播Url ,
            parameters (string, optional): 直播参数 ,
            liveSportsItemId (integer, optional): 所属直播节目Id ,
            id (integer, optional) -->
        <div class="live-header clearfix">
            <div class="pull-left live-team home-live-team">
                <div class="live-team-logo"><img src="../../static/images/home-live-team-logo.png" alt=""></div>
                <div class="pull-left live-team-name text-hidden text-left">广州恒大淘宝</div>
                <div class="pull-right live-team-score hide">2</div>
            </div>
            <div class="pull-left live-info">
                <div class="live-time">格伦杯  18/08/08  09:30</div>
                <p class="live-type">进行中</p>
                <p class="live-vs">VS</p>
            </div>
            <div class="pull-left live-team away-live-team">
                <div class="live-team-logo"><img src="../../static/images/away-live-team-logo.png" alt=""></div>
                <div class="pull-right live-team-name text-hidden text-right">上海绿地申花</div>
                <div class="pull-left live-team-score hide">1</div>
            </div>
        </div>
        <div class="live-body clearfix">
            <div class="pull-left live-body-left">
                <div class="iframe-box"><iframe width="100%" height="100%" :src="liveUrl" frameborder="0"></iframe></div>
                <div class="live-share">
                    <div class="pull-left"><span class="text-white">遇到直播问题，请即时联系客服微信：76354365</span> <span class="text-red">广告加微信更多比赛推荐赛事328748323423</span></div>
                    <a class="pull-right text-white share" href="javascript:;">分享网站</a>
                </div>
            </div>
            <!--<div class="pull-right live-body-right">-->
                <!--<div class="chat-box">-->
                    <!--<div class="chat-title">-->
                        <!--<img src="images/chat-title-bg.png" width="82" height="16" alt="">-->
                    <!--</div>-->
                <!--</div>-->
            <!--</div>-->
        </div>
        <div class="live-footer">
            <ul class="clearfix">
                <li v-for="(item, index) in liveSportsItemSources"
                  :key="index"
                  :class="[activeIndex == index ? 'active' : '']"
                  @click="changeLiveUrl(index)">
                  <a href="javascript:;">{{item.name}}</a>
                </li>
            </ul>
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      liveSportsItemSources: 'liveSportsItemSources'
    }),
    liveUrl () {
      if (!this.liveSportsItemSources[this.activeIndex]) return ''
      return this.liveSportsItemSources[this.activeIndex].liveUrl
    }
  },
  methods: {
    // ...mapActions([
    //   ''
    // ])
    changeLiveUrl (index) {
      this.activeIndex = index
    }
  },
  created () {
  }
}
</script>

<style>

</style>



