<template>
  <div class="match-nav-box">
      <ul class="clearfix">
          <li v-for="(item, index) in sportsNewsCategories"
            :class="[index == activeIndex ? 'active' : '']"
            :key="item.id"
            @click="changeSportsNewsCategory(item.id, index)"
            v-if="activeHeaderNav == 'news'">
            <a href="javascript:void(0);">{{item.cnName}}</a>
          </li>
          <li v-for="(item, index) in highlightsVideoCategories"
            :class="[index == activeIndex ? 'active' : '']"
            :key="item.id"
            @click="changeHighlightsVideoCategory(item.id, index)"
            v-if="activeHeaderNav == 'video'">
            <a href="javascript:void(0);">{{item.cnName}}</a>
          </li>
          <li v-for="(item, index) in channelCategories"
            :class="[index == activeIndex ? 'active' : '']"
            :key="item.id"
            @click="changeChannelCategory(item.id, index)"
            v-if="activeHeaderNav == 'channel'">
            <a href="javascript:void(0);">{{item.name}}</a>
          </li>
      </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  computed: {
    ...mapGetters({
      activeHeaderNav: 'activeHeaderNav',
      sportsNewsCategories: 'sportsNewsCategories',
      highlightsVideoCategories: 'highlightsVideoCategories',
      channelCategories: 'channelCategories'
    })
  },
  methods: {
    ...mapActions([
      'setSportsNewsByCategory',
      'setSportsNewsCategoryId',
      'setHighlightsSportsItemCategoryId',
      'setHighlightsSportsItemByCategory',
      'setChannelByCategory'
    ]),
    changeSportsNewsCategory (id, index) {
      this.activeIndex = index
      let params = {
        categoryId: id,
        skipCount: 0,
        maxResultCount: 20
      }
      this.setSportsNewsCategoryId(id)
      this.setSportsNewsByCategory({ params: params })
    },
    changeHighlightsVideoCategory (id, index) {
      this.activeIndex = index
      let params = {
        categoryId: id,
        skipCount: 0,
        maxResultCount: 20
      }
      this.setHighlightsSportsItemCategoryId(id)
      this.setHighlightsSportsItemByCategory({ params: params })
    },
    changeChannelCategory (id, index) {
      this.activeIndex = index
      // this.setChannelCategoryId(id)
      this.setChannelByCategory(id)
    }
  }
}
</script>

<style>

</style>



