<template>
  <div>
    <app-header></app-header>
    <live-main></live-main>
    <fun></fun>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import header from '../components/public/header'
import liveMain from '../components/liveMain'
import fun from '../components/fun'
import footer from '../components/public/footer'
export default {
  components: {
    'app-header': header,
    'live-main': liveMain,
    'fun': fun,
    'app-footer': footer
  },
  methods: {
    ...mapActions([
      'setFanCommunityCategories',
      'setLiveSportsItemSources'
    ])
  },
  created () {
    let videoCategory = this.$route.params.videoCategory
    let videoId = this.$route.params.videoId
    this.setFanCommunityCategories()
    let params = {
      // videoCategory (integer, optional): 视频分类, 0:直播 1:录像 2:集锦 99:其他 = ['0', '1', '2', '99'],
      // id (integer, optional)
      videoCategory: videoCategory,
      id: videoId
    }
    this.setLiveSportsItemSources(params)
  }
}
</script>

<style>
@import "../../static/css/live/live.css";
</style>



