<template>
  <div class="article-box">
      <!-- title (string, optional): 标题 ,
        source (string, optional): 来源 ,
        content (string, optional): 内容 ,
        publishTime (string, optional): 发布时间 ,
        id (integer, optional) -->
      <h1 class="title">{{article.title}}</h1>
      <span class="text-999 fs12">{{article.date}}   来源:{{article.source}}</span>
      <div class="article-info" v-html="article.content"></div>
      <div class="announce">声明：以上文章内容仅代表作者本人观点，不代表A8体育点观点或立场。转载声明、版权声明</div>
      <div class="ban-box">
          <span class="tag">标签</span>
          <span class="tag"> <a href="javascript:;" target="_blank">世界杯</a></span>
          <span class="tag"> <a href="javascript:;" target="_blank">世界杯</a></span>
          <span class="tag"> <a href="javascript:;" target="_blank">世界杯</a></span>
          <span class="tag"> <a href="javascript:;" target="_blank">世界杯</a></span>
      </div>
      <div class="share-box">
          <span class="tag">分享</span>
      </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { formatToyyyyMMdd8 } from '@/utils'
export default {
  computed: {
    ...mapGetters({
      sportsNewsContent: 'sportsNewsContent'
    }),
    article () {
      let article = this.sportsNewsContent
      if (!article.title) return {}
      article.date = formatToyyyyMMdd8(article.publishTime)
      return article
    }
  }
}
</script>

<style>

</style>



